@import url(https://fonts.googleapis.com/css?family=Space+Mono&display=swap);
body {
  margin: 0;
  font-family: "Space Mono", -apple-system, BlinkMacSystemFont, "Segoe UI",
    "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
    "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 20px;
}

@media screen and (max-width: 450px) {
  body {
    font-size: 14px;
  }
}

.header {
  position: absolute;
  text-align: center;
  width: 100%;
}

.links {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}

.links h1 {
  cursor: pointer;
}

.underline {
  text-decoration: overline;
}

.contact {
  line-height: 2em;
}

.contact .about {
  position: relative;
}

.portfolio {
  display: flex;
  flex-direction: row;
}

@media screen and (max-width: 450px) {
  .portfolio {
    display: flex;
    flex-direction: column;
  }
}

.portfolioLinks {
  flex-direction: "column";
  flex-grow: 1;
  width: 33%;
  cursor: pointer;
}
@media screen and (max-width: 750px) and (min-width: 451px) {
  .portfolioLinks {
    width: 20%;
  }
}
@media screen and (max-width: 450px) {
  .portfolioLinks {
    width: 100%;
  }
}

.portfolioImages {
  flex-direction: "column";
  flex-grow: 1;
  width: 25%;
}
@media screen and (max-width: 450px) {
  .portfolioImages {
    width: 100%;
    margin: 0 auto;
  }
}

.portfolioDesc {
  flex-direction: "column";
  flex-grow: 1;
  width: 24%;
  margin-left: 5px;
}
@media screen and (max-width: 450px) {
  .portfolioDesc {
    width: 100%;
    margin: 0 auto;
  }
}

.spacer {
  width: 100%;
  height: 7em;
}

.container {
  margin: 1em 2em;
}

h1 {
  font-size: 26px;
}

@media screen and (max-width: 450px) {
  h1 {
    font-size: 14px;
  }
}

a {
  text-decoration: none;
  color: inherit;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

